@media (min-width: 992px) {
  #menu-1 > li > a {
    pointer-events: none;
  }
}
.hidden-header-wrapper {
  display: none;
}

.hidden-entry-header-wrapper {
  display: none;
}

.blurb-post-wrapper {
  margin-bottom: 30px;
}

.archive-entry-image {
  float: left;
  max-width: 120px;
  max-height: 120px;
  margin-right: 25px;
  margin-bottom: 35px;
  border: 1px solid black;
}

.btn-grey {
  margin: 0 0.25rem;
  border: 1px solid #979797;
  font-weight: 600;
}

.site-footer {
  overflow: hidden;
}

.footer-news-row {
  background-color: #262223;
  padding: 1rem;
}

.footer-news-col {
  margin: 1rem auto;
  max-width: max-content;
  width: 100%;
}

.news-header {
  color: #f2f2f2;
}

.news-submit-input {
  width: 100%;
  color: #f2f2f2;
  background-image: -webkit-linear-gradient(top, #1a7fb6, #158FBF);
  border: none;
}

.news-submit-input:hover {
  color: #1a7fb6;
  font-weight: 600;
  background-image: -webkit-linear-gradient(top, #f2f2f2, #f4f4f4);
  border: none;
}

.footer-info-row {
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  font-size: 0.85rem;
  color: var(--dark);
  background-color: #f2f2f2;
}

.footer-divider {
  padding: 0 5px;
}

.charity-col {
  max-width: max-content;
  width: 100%;
}

.credit-col {
  display: flex;
  justify-content: center;
  max-width: max-content;
  width: 100%;
}

.link-col {
  display: flex;
  justify-content: flex-end;
  max-width: max-content;
  width: 100%;
}

.footer-link {
  color: #262223 !important;
}

.footer-link:hover {
  color: rgba(38, 34, 35, 0.7) !important;
}

